"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"

import { Card, CardHeader, CardTitle, CardDescription, CardContent } from "@/components/ui/card"
import { Table, TableHeader, TableRow, TableHead, TableBody, TableCell } from "@/components/ui/table"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"

import { Search, Edit, Trash2, UserPlus } from "lucide-react"
import { useToast } from "@/components/ui/use-toast"

export default function AdminMentorManagementPage() {
  const supabase = createClient()
  const { toast } = useToast()

  const [mentors, setMentors] = useState<any[]>([])
  const [filteredMentors, setFilteredMentors] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [search, setSearch] = useState("")

  useEffect(() => {
    const loadMentors = async () => {
      setLoading(true)
      try {
        // fetch mentor profiles + extra mentor data
        const { data, error } = await supabase
          .from("mentor_profiles")
          .select(`
            id,
            profile_id,
            specialization,
            years_of_experience,
            rating,
            total_students,
            availability,
            profiles(full_name, email, phone, avatar_url)
          `)

        if (error) throw error

        setMentors(data || [])
        setFilteredMentors(data || [])
      } catch (err: any) {
        toast({
          title: "Error",
          description: err.message,
          variant: "destructive",
        })
      } finally {
        setLoading(false)
      }
    }

    loadMentors()
  }, [])

  // search filter
  useEffect(() => {
    const result = mentors.filter((m) =>
      (m.profiles?.full_name || "").toLowerCase().includes(search.toLowerCase()) ||
      (m.profiles?.email || "").toLowerCase().includes(search.toLowerCase())
    )
    setFilteredMentors(result)
  }, [search, mentors])

  const deleteMentor = async (mentorId: string) => {
    const ok = confirm("Are you sure you want to delete this mentor?")
    if (!ok) return

    try {
      const { error } = await supabase
        .from("mentor_profiles")
        .delete()
        .eq("id", mentorId)

      if (error) throw error

      setMentors((prev) => prev.filter((m) => m.id !== mentorId))
      setFilteredMentors((prev) => prev.filter((m) => m.id !== mentorId))

      toast({
        title: "Mentor removed",
        description: "The mentor has been deleted successfully.",
      })
    } catch (err: any) {
      toast({
        title: "Error deleting mentor",
        description: err.message,
        variant: "destructive",
      })
    }
  }

  return (
    <div className="p-8">
      <h2 className="text-3xl font-bold mb-4">Mentor Management</h2>
      <p className="text-muted-foreground mb-6">Manage all mentors in the academy</p>

      <Card className="border-0 shadow-sm">
        <CardHeader>
          <CardTitle>Mentors</CardTitle>
          <CardDescription>Total: {filteredMentors.length} mentors</CardDescription>
        </CardHeader>

        <CardContent className="space-y-4">
          {/* Search + Add */}
          <div className="flex gap-2">
            <div className="relative flex-1">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
              <Input
                placeholder="Search mentors by name or email..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                className="pl-10"
              />
            </div>

            <Button className="gap-2">
              <UserPlus className="w-4 h-4" />
              Add Mentor
            </Button>
          </div>

          {/* Table */}
          <div className="border rounded-lg overflow-hidden">
            {loading ? (
              <div className="text-center py-8 text-muted-foreground">Loading mentors...</div>
            ) : filteredMentors.length === 0 ? (
              <div className="text-center py-8 text-muted-foreground">No mentors found</div>
            ) : (
              <Table>
                <TableHeader>
                  <TableRow className="bg-secondary/50">
                    <TableHead>Name</TableHead>
                    <TableHead>Email</TableHead>
                    <TableHead>Specialization</TableHead>
                    <TableHead>Exp</TableHead>
                    <TableHead>Rating</TableHead>
                    <TableHead>Students</TableHead>
                    <TableHead className="text-right">Actions</TableHead>
                  </TableRow>
                </TableHeader>

                <TableBody>
                  {filteredMentors.map((mentor) => (
                    <TableRow key={mentor.id}>
                      <TableCell className="font-medium">
                        {mentor.profiles?.full_name || "Unknown"}
                        <div className="text-xs text-muted-foreground">
                          {mentor.profiles?.phone || "No phone"}
                        </div>
                      </TableCell>

                      <TableCell>{mentor.profiles?.email}</TableCell>

                      <TableCell>
                        <Badge variant="outline">{mentor.specialization || "N/A"}</Badge>
                      </TableCell>

                      <TableCell>{mentor.years_of_experience || 0} yrs</TableCell>

                      <TableCell>
                        ‚≠ê {Number(mentor.rating).toFixed(1)}
                      </TableCell>

                      <TableCell>
                        {mentor.total_students ?? 0}
                      </TableCell>

                      <TableCell className="text-right space-x-2">
                        <Button size="sm" variant="ghost">
                          <Edit className="w-4 h-4" />
                        </Button>

                        <Button
                          size="sm"
                          variant="ghost"
                          className="text-destructive"
                          onClick={() => deleteMentor(mentor.id)}
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
